.matchedProfile {
    margin-top: 10px;
    background-color: white;
    width: 95%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    border-radius: 10px;
    margin-bottom: 10px;
    box-shadow: 2px 1px 10px lightgrey;
    padding: 10px;
}

.matchedProfileTop {
    width: 100%;
    display: flex;
    flex-direction: row;
    margin-bottom: -10px;
}
.matchedProfileTopLeft {
    padding-top: 10px;
    width: 30%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.matchedProfileTopRight {
    width: 70%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding-left: 5px;
}

.matchedProfileNameText {
    width: 100%;
    display: flex;
    align-items: flex-end;
    font-size: 30px;
    font-weight: 400;
}

.matchedProfileSocialMedia {
    margin-top: 5px;
    width: 100%;
    display: flex;
    align-items: center;
}

.matchedProfileBottom {
    margin-top: -15px;
    width: 100%;
    text-align: center;
}

.interestTagText {
    font-weight: 400;
    color: rgb(83, 100, 113);
}
.likeContainer {
    display: inline-block;
    color: white;
    font-weight: 500;
    margin: 3px 3px 3px 0px;
    padding: 5px 10px 5px 10px;
    border-radius: 50px;
    background-color: var(--themeLightGreen);    
    box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
}